<template>
  <div v-bind:style="st" id="totop">
    <button class="totop-btn" @click="scrollUp()">
      <img class="totop-img" src="../images/totop.svg" alt="&#8593;" />
    </button>
  </div>
</template>

<script>
export default {
  name: "totop",
  data: function () {
    return {
      st: {
        visibility: "hidden",
        opacity: "0",
      },
    };
  },
  created: function () {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      if (document.body.scrollTop > 1 || document.documentElement.scrollTop > 1) {
        this.st = { visibility: "visible", opacity: 1 };
      } else {
        this.st = { visibility: "hidden", opacity: 0 };
      }
    },
    scrollUp: function () {
      console.log("scroll to top");
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
  },
};
</script>

<style scoped>
#totop {
  z-index: 3;
  position: fixed;
  bottom: 18px;
  right: 32px;
  transition: visibility 200ms, opacity 200ms linear;
}

.totop-btn {
  cursor: pointer;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  background-color: transparent;
  border: none;
  font-size: 34px;
  outline: none;

  /* -webkit-box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75); */
  /* border-radius: 50%;
  width: 48px;
  height: 48px;
  background-color: white;
  border: none;
  font-size: 34px;
  outline: none;
  -webkit-box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75); */
}

.totop-btn:active:hover {
  background-color: #aaa;
  background-color: transparent;
}
</style>
