<template>
  <div id="navbar" class="navbar">
    <div id="banner-container" class="banner-container">
      <img src="../images/xs-logo-48.svg" alt="Logo" id="banner-img" style="" />
      <div id="banner-txt">Siotou Xeni</div>
    </div>
    <div id="nav-container" class="nav-container pos1">
      <router-link class="nav-button" to="/home" exact>HOME</router-link>
      <router-link class="nav-button" to="/architecture" exact>ARCHITECTURE</router-link>
      <router-link class="nav-button" to="/dressmaking" exact>DRESSMAKING</router-link>
      <router-link class="nav-button" to="/handicraft" exact>HANDICRAFT</router-link>
      <router-link class="nav-button" to="/about" exact>ABOUT</router-link>
    </div>
  </div>
</template>

<script>
export default {
  watch: {
    $route(to, from) {
      if (from.path.includes("home")) {
        document.getElementById("nav-container").classList.remove("pos" + 1);
      }
      if (from.path.includes("architecture")) {
        document.getElementById("nav-container").classList.remove("pos" + 2);
      }
      if (from.path.includes("dressmaking")) {
        document.getElementById("nav-container").classList.remove("pos" + 3);
      }
      if (from.path.includes("handicraft")) {
        document.getElementById("nav-container").classList.remove("pos" + 4);
      }
      if (from.path.includes("about")) {
        document.getElementById("nav-container").classList.remove("pos" + 5);
      }
      if (to.path.includes("home")) {
        document.getElementById("nav-container").classList.add("pos" + 1);
      }
      if (to.path.includes("architecture")) {
        document.getElementById("nav-container").classList.add("pos" + 2);
      }
      if (to.path.includes("dressmaking")) {
        document.getElementById("nav-container").classList.add("pos" + 3);
      }
      if (to.path.includes("handicraft")) {
        document.getElementById("nav-container").classList.add("pos" + 4);
      }
      if (to.path.includes("about")) {
        document.getElementById("nav-container").classList.add("pos" + 5);
      }
    },
  },
};

//navbar hide functionality
var prevScrollpos = window.pageYOffset;
window.onscroll = function () {
  var currentScrollPos = window.pageYOffset;
  if (prevScrollpos > currentScrollPos) {
    document.getElementById("navbar").style.top = "0";
  } else {
    document.getElementById("navbar").style.top = "-120px";
  }
  prevScrollpos = currentScrollPos;
};
</script>

<style scoped>
#navbar {
  font-family: "Open Sans";
  background-color: white;
  border-bottom: 2px solid black;
  /* -webkit-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25); */
  position: fixed;
  top: 0;
  width: 100%;
  display: block;
  transition: top 0.5s;
  z-index: 1;
}

.nav-container {
  display: none;
  margin-right: 10%;
  float: right;
  position: relative;
}
.nav-container::after {
  width: 12px;
  height: 12px;
  position: absolute;
  border-radius: 50%;
  content: url("../images/circle.svg");
  line-height: 8px;
  bottom: -5px;
  -webkit-transition: left 0.5s;
  transition: left 0.5s;
  font-size: 25px;
}

.nav-container.pos1::after {
  left: calc(2vw + 22px);
}
.nav-container.pos2::after {
  left: calc(4vw + 113px);
}

.nav-container.pos3::after {
  left: calc(6vw + 257px);
}
.nav-container.pos4::after {
  left: calc(8vw + 377px);
}
.nav-container.pos5::after {
  left: calc(10vw + 469px);
}

/* .router-link-exact-active {
  text-decoration: underline !important;
} */

.banner-container {
  height: 48px;
  padding: 7px 0px;
  float: left;
  margin-left: 10%;
}

#banner-img {
  display: block;
  height: 48px;
  display: inline-block;
}

#banner-txt {
  display: inline-block;
  width: 180px;
  text-align: right;
  line-height: 53px;
  vertical-align: top;
  font-size: 31px;
  font-family: "Raleway", cursive;
}

.nav-button {
  display: inline-block;
  background-color: rgba(255, 255, 255, 0);
  color: black;
  font-size: 16px;
  letter-spacing: 2px;
  padding: 20px 0px 20px 2vw;
  text-decoration: none;
}

.nav-button:hover:active {
  background-color: white;
  /* text-decoration: underline; */
  color: rgb(0, 0, 0);
}
.nav-button:hover {
  color: rgb(97, 97, 97);
}

@media only screen and (min-width: 1065px) {
  .nav-container {
    display: inline-block;
  }
  .banner-container {
    height: 48px;
  }
  #banner-txt {
    /* line-height: 64px; */
  }
}
/* @media only screen and (min-width: 1280px) {
  .nav-container {
    margin-right: 14%;
  }
  .banner-container {
    margin-left: 22%;
  }
} */

@media only screen and (max-width: 1064px) {
  .banner-container {
    float: inherit;
    margin-left: auto;
    margin-right: auto;
    width: 225px;
    padding: 5px 0px;
  }
}
</style>
