.<template >
  <div id="sidebar">
    <button id="sidebar-burger" class="sidebar-burger" @click="toggle">&#9776;</button>
    <div class="nav show" @click="close" v-bind:class="{ hide: isHidden }">
      <router-link class="nav-button" to="/home" v-on:click.native="close">HOME</router-link>
      <router-link class="nav-button" to="/architecture" v-on:click.native="close"> ARCHITECTURE </router-link>
      <router-link class="nav-button" to="/dressmaking" v-on:click.native="close"> DRESSMAKING </router-link>
      <router-link class="nav-button" to="/handicraft" v-on:click.native="close"> HANDICRAFT </router-link>
      <router-link class="nav-button" to="/about" v-on:click.native="close"> ABOUT </router-link>
    </div>
    <div class="overlay show" v-bind:class="{ hide: isHidden }" @click="close"></div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      isHidden: true,
    };
  },
  methods: {
    toggle: function () {
      if (this.isHidden) {
        document.getElementById("sidebar-burger").innerText = "\u26CC";
        document.getElementById("sidebar-burger").classList.add("sb-x");
      } else {
        document.getElementById("sidebar-burger").innerText = "\u2630";
        document.getElementById("sidebar-burger").classList.remove("sb-x");
      }

      this.isHidden = !this.isHidden;
    },
    close: function () {
      this.isHidden = true;
      document.getElementById("sidebar-burger").innerText = "\u2630";
      document.getElementById("sidebar-burger").classList.remove("sb-x");
    },
    accordionToggle: function () {
      //TODO logic to show/hide router-links in the menu here
      //need to stop propagation so that the nav @click won't close the sidebar
      event.stopPropagation();
    },
  },
};
</script>

<style scoped>
#sidebar {
  display: block;
}

@media only screen and (min-width: 1065px) {
  #sidebar {
    display: none;
  }
}

.sidebar-burger {
  color: #747474;
  font-size: 32px;
  padding: 10px;
  line-height: 1em;
  z-index: 6;
  position: fixed;
  top: 3px;
  left: 3px;
  background-color: transparent;
  border: none;
  outline-color: transparent;
  -webkit-transition: background-color 180ms linear;
  transition: background-color 180ms linear;
}

.sidebar-burger:active:hover {
  border: 1px solid black;
  color: rgb(97, 97, 97);
}

.sb-x {
  background-color: transparent;
  color: white;
  animation: spin 0.3s linear;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}

.nav {
  background-color: rgb(50, 50, 50);
  text-align: center;
  width: 270px;
  height: 100%;
  display: block;
  z-index: 5;
  position: fixed;
  top: 0px;
  padding-top: 120px;
  visibility: visible;
  opacity: 1;
  transition: visibility 0s linear 0s, opacity 300ms;
}
.nav.hide {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 300ms, opacity 300ms;
}
.router-link-active::before {
  content: "- ";
}
.router-link-active::after {
  content: " -";
}

.overlay {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 4;

  visibility: visible;
  opacity: 1;
  transition: visibility 0s linear 0s, opacity 300ms;
}
.overlay.hide {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 300ms, opacity 300ms;
}

.nav-button {
  display: block;
  background-color: rgba(255, 255, 255, 0);
  color: white;
  text-decoration: none;
  font-size: 16px;
  letter-spacing: 2px;
  padding: 10px 20px;
  margin-bottom: 14px;
}
.nav-button:hover {
  background-color: rgb(255, 255, 255);
  color: black;
}

@media (max-width: 600px) {
  .nav {
    width: 100%;
  }
}
</style>
